
*,*:before,*:after{box-sizing:border-box;-webkit-tap-highlight-color:transparent
}
article,aside,details,figcaption,figure,footer,header,hgroup,nav,section,summary{display:block
}
audio,canvas,video{display:inline-block
}
[hidden],template{display:none
}
html,button,input,select,textarea{font-family:sans-serif;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;-moz-font-feature-settings:"liga", "kern"
}
body{margin:0
}
a{background:transparent
}
a:hover,a:active{outline:0
}
b,strong{font-weight:bold
}
code,kbd,pre,samp{font-family:monospace, serif;font-size:1em
}
pre{white-space:pre;white-space:pre-wrap;word-wrap:break-word
}
small{font-size:80%
}
a,img,button,input,select,textarea,label{font-family:inherit;font-size:100%;margin:0;line-height:inherit;outline:0;outline:medium none
}
button,input{line-height:normal
}
button,select{text-transform:none
}
button,html input[type="button"],input[type="reset"],input[type="submit"]{-webkit-appearance:none;cursor:pointer;outline:0
}
button[disabled],input[disabled]{cursor:default
}
input[type="checkbox"],input[type="radio"]{box-sizing:border-box;padding:0
}
input[type="search"]{-webkit-appearance:none
}
input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration{-webkit-appearance:none
}
input[type="number"]::-webkit-inner-spin-button,input[type="number"]::-webkit-outer-spin-button{height:auto
}
button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0
}
textarea{overflow:auto;vertical-align:top
}
optgroup{font-weight:bold
}
table{border-collapse:collapse;border-spacing:0
}
html{-webkit-overflow-scrolling:touch;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;-webkit-touch-callout:none;-webkit-user-select-none:none;user-select-none:none
}
body{font-family:"PingFangSC-Regular",Helvetica,sans-serif;font-size:.28rem;line-height:1.5;color:#333;background-color:#eee
}
img{vertical-align:middle;width:100%;height:auto;border:0;-webkit-touch-callout:none
}
textarea{resize:vertical
}
h1,h2,h3,h4,h5,h6,p,figure,form{margin:0
}
ul,ol,li,dl,dd{margin:0;padding:0;list-style:none outside none
}
a{text-decoration:none;color:#333
}
input::-webkit-input-placeholder,textarea::-webkit-input-placeholder{color:#ccc
}
[v-cloak]{display:none
}
label>*{pointer-events:none
}
@font-face{font-family:"blfont";src:url("data:;base64,") format("truetype")
}
.iconfont{font-family:"blfont";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-style:normal;font-variant:normal;font-weight:normal;text-decoration:none;text-transform:none;line-height:normal;text-rendering:auto
}
.arrow-back:before{content:"\E001"
}
.arrow-right:before{content:"\E002"
}
.ask:before{content:"\E003"
}
.askfill:before{content:"\E004"
}
