
.mint-swipe {
  overflow: hidden;
  position: relative;
  height: 100%;
}
.mint-swipe-items-wrap {
  position: relative;
  overflow: hidden;
  height: 100%;
}
.mint-swipe-items-wrap > div {
  position: absolute;
  transform: translateX(-100%);
  width: 100%;
  height: 100%;
  display: none
}
.mint-swipe-items-wrap > div.is-active {
  display: block;
  transform: none;
}
.mint-swipe-indicators {
  position: absolute;
  bottom: 10px;
  right: 10px;
  /* left: 50%;
  -webkit-transform: translateX(-50%); */
  /* transform: translateX(-50%); */
  font-size: 0;
}
.mint-swipe-indicator {
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 100%;
  background: #ccc;
  opacity: .8;
  margin: 0 3px;
}
.mint-swipe-indicator.is-active {
  background: #ff5501;
}



