
.j-nav{width:.8rem
}
.j-nav div.button{transition:all 0.5s ease;position:absolute;right:0;top:0;font-size:.4rem
}
.j-nav div.error{visibility:hidden;opacity:0;color:#fe5b4a !important;font-size:.35rem
}
.j-nav.show-nav .more-v{visibility:hidden;opacity:0
}
.j-nav.show-nav .error{visibility:inherit;opacity:1
}
.j-navcontent{position:absolute;width:100%;top:.88rem;left:0;right:0;height:0;visibility:hidden;transition:all 0.2s linear;border-bottom:.04rem solid #fe8477
}
.j-navcontent:before{content:'';position:absolute;top:-5px;right:.3rem;width:0;height:0;border-style:solid;border-width:0 6px 6px 6px;border-color:transparent transparent rgba(0,0,0,0.8) transparent
}
.j-navcontent.show-navcontent{height:1.2rem;visibility:inherit
}
.j-navcontent .ul-nav{display:-moz-flex;display:-ms-flexbox;display:flex;box-pack:center;-moz-justify-content:center;-ms-flex-pack:center;justify-content:center;padding:.18rem 0;overflow:hidden;height:100%;background-color:rgba(0,0,0,0.8);color:#ffffff
}
.j-navcontent .ul-nav li{box-flex:1;-moz-flex:1;-ms-flex:1;flex:1;-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center;width:0;text-align:center
}
.j-navcontent .ul-nav li a{color:#ffffff;display:block
}
.j-navcontent .ul-nav li p{font-size:.24rem
}
.j-navcontent .ul-nav li i{font-size:.4rem;line-height:1;vertical-align:middle
}



