
.footer,.footer .footer-item{display:-moz-flex;display:-ms-flexbox;display:flex;box-align:center;-moz-align-items:center;-ms-flex-align:center;align-items:center
}
.footer+.page-content,.footer ~ .page-content{bottom:1rem
}
.footer+.page{bottom:1rem
}
.footer{position:absolute;z-index:10000;bottom:0;left:0;right:0;width:100%;height:1rem;border:none;background-image:linear-gradient(0deg, #d8d8d8, #d8d8d8 50%, transparent 50%);background-position:top;background-repeat:no-repeat;background-origin:content-box;background-size:100% 1px
}
.footer.footer-light{background-color:#f9f5f4
}
.footer .footer-item{box-flex:1;-moz-flex:1;-ms-flex:1;flex:1;width:0;box-pack:center;-moz-justify-content:center;-ms-flex-pack:center;justify-content:center;text-align:center;height:100%
}
.footer .footer-item a{display:block;font-size:0
}
.footer .footer-item i{font-size:.45rem;line-height:1;color:#555555
}
.footer .footer-item .tab-tro{font-size:.24rem;color:#333333;line-height:1;margin-top:.1rem
}
.footer .footer-item.active i,.footer .footer-item.active .tab-tro{color:#fc511e
}



