
.tag{position:relative;padding:0 .09rem;font-style:normal;font-weight:normal;line-height:1.5;font-size:.24rem;display:inline-block;transform:scale(0.84);transform-origin:left center
}
.tag:after{content:'';position:absolute;top:0;left:0;border:1px solid #c5c5c5;box-sizing:border-box;width:200%;height:200%;transform:scale(0.5);transform-origin:left top;border-radius:3px
}
.tag.orange{color:#d91c29
}
.tag.orange:after{border:1px solid #d91c29
}



